// Toggle integration confirmation modal
.ember-modal-overlay .ember-modal-dialog.toggle-integration-confirmation-modal {
	animation: 0.2s ease-in-out softLoad forwards;
	border-radius: 4px;
	box-shadow: 0 2px 10px 0 $app-info-color;
	top: 35% !important;
	width: 512px;

	.ember-modal-body {
		.note {
			border-radius: 4px;
			line-height: $baseLineHeight;
			margin-bottom: 24px;
			padding: 4px 8px 8px;

			.title { font-size: $baseFontSize; font-weight: $text-weight-semi-bold; }
		}

		.text { font-weight: $text-weight-semi-bold; }
	}

	.ember-modal-footer {
		.btn { min-width: 60px; }
	}
}